  .container {
    max-width: none;
    padding: 0 10px;
  }
}

.wrapper {
  width: 100%;
  max-width: 1716px;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}
h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

.grid {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  gap: 30px;
}
