/* 01-hero ================================================== */
.hero {
  width: 100%;
  margin-bottom: -50px;
}
.hero__wrapper {
  margin: 0 auto;
  border-radius: 41px;
  background-color: #ffffff;
}
.hero__line {
  background-image: url("/img/hero1.png");
  background-repeat: no-repeat;
  background-position: center right;
  background-size: 60%;
  width: 100%;
}
@media screen and (max-width: 767.98px) {
  .hero__line {
    background-position: right top 60%;
  }
}
@media screen and (max-width: 570px) {
  .hero__line {
    background-position: center top 80%;
  }
}
.hero__offer {
  margin: 0 20px;
}
.hero__title {
  font-size: 110px;
  line-height: 1.1090909091;
  padding-top: 66px;
  font-family: "NovaSquare";
  font-weight: 400;
  color: #000000;
  max-width: 600px;
  margin-bottom: 32px;
}
@media screen and (max-width: 992.98px) {
  .hero__title {
    font-size: 80px;
    padding-top: 30px;
    margin-bottom: 20px;
  }
}
@media screen and (max-width: 570px) {
  .hero__title {
    font-size: 70px;
  }
}
.hero__descr {
  font-size: 25px;
  line-height: 1.32;
  margin-bottom: 53px;
  color: #000000;
  max-width: 380px;
}
.hero__btn {
  margin-bottom: 192px;
}
@media screen and (max-width: 570px) {
  .hero__btn {
    margin-bottom: 260px;
  }
}
.hero__tabs {
  margin-left: 20px;
  margin-right: 20px;
  margin-top: -100px;
}

.tab__top {
  display: flex;
}
.tab__link {
  display: block;
  font-size: 20px;
  line-height: 1.3;
  margin: 0;
  padding: 24px 62px;
  background-color: #272727;
}
@media screen and (max-width: 570px) {
  .tab__link {
    width: 50%;
  }
}
.tab__link:first-child {
  border-radius: 11px 0px 0px 0px;
}
.tab__link:last-child {
  border-radius: 0px 11px 0px 0px;
}
.tab__link:focus-within {
  background-color: #484848;
}
.tab__form {
  width: 100%;
  background-color: #484848;
  padding: 10px 10px 10px 40px;
  border-radius: 0px 11px 11px 11px;
  font-size: 18px;
  line-height: 24px;
}
@media screen and (max-width: 570px) {
  .tab__form {
    border-radius: 0px 0px 11px 11px;
    margin-bottom: 20px;
    padding-bottom: 20px;
  }
}
.tab__flex {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  gap: 40px;
}
@media screen and (max-width: 992.98px) {
  .tab__flex {
    grid-template-columns: repeat(6, 1fr);
  }
}
@media screen and (max-width: 570px) {
  .tab__flex {
    grid-template-columns: 1fr;
  }
}
.tab__label {
